html, body, #root{
    width: 100%;
    height: 100%;
}

.optionContainer {
    width: 100%;
    height: 50%;
    font-size: 20px;
}


.optionLeft, .optionRight{
    width: 50%;
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* 페이드 인/아웃  */
@keyframes fade-out {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.fade-in {
    animation: fade-in 0.5s ease-in forwards;
}

.fade-out {
    animation: fade-out 0.5s ease-out forwards;
}
/* end of 페이드 인/아웃  */


.optionLeft{
    background-color: rgb(255, 85, 85);
    border: solid 10px red;
    color: antiquewhite;
}

.optionRight{
    background-color: rgb(85, 85, 255);
    border: solid 10px blue;
    color: antiquewhite;
}

